.stack {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}
p.work {
    font-size: 12px;
}
.stack img {
    width: 50px;
    height: 50px;
    padding: 7.5px;
    filter: drop-shadow(-2px 2px 2px #777);
}
.stack img:hover {
    width: 65px;
    height: 65px;
    padding: 5px;
}
.other-work p {
    font-size: 12px;
    line-height: 12px;
}
.other-work p * {
    vertical-align: middle;
}
@media screen and (max-width: 540px){
    .projects {
        min-height: 200px;
        margin: 20px 0;
    }
    .old-projects.welcome ul {
        padding-left: 10px;
    }
    .stack img {
        width: 46px;
        height: 46px;
    }
}